/* sorok */
/* Для всех элементов */
* {
  user-select: none;
  -webkit-user-select: none;  /* Chrome/Safari */
  -moz-user-select: none;     /* Firefox */
  -ms-user-select: none;      /* IE/Edge */
}

/* Для изображений (опционально) */
img {
  pointer-events: none; /* Блокирует взаимодействие с картинкой */
}

html {
  overflow: hidden !important;
  height: 100%;
  width: 100%;
  position: relative;
}

@font-face {
  font-family: Sorok;
  src: url("/public/itccharterpro.otf");
}

@font-face {
  font-family: Rodnik;
  src: url("/public/Rodnik.otf");
}

.sorokust {
  background-image: url("/public/sorok-bg.png");
  width: 100%;
  height: 100vh;
  height: -webkit-fill-available;
  overflow: hidden !important;
  position: fixed;
  top: env(safe-area-inset-top); /* Учитываем вырез камеры */
  left: 0;
  padding-bottom: env(safe-area-inset-bottom); /* Для нижних индикаторов */
}

.ant-otp-input {
  font-family: "Rodnik";
  font-weight: bold;
  background-color: transparent;
  color: white;
  font-weight: 14px;
}

.ant-otp-input:hover,
.ant-otp-input:focus {
  background-color: transparent;
  border-color: #d9d9d9;
}

.calculationHeaders {
  font-family: "Sorok";
  font-size: 18px;
  color: white !important;
  font-weight: 500;
}

.calculationDetails {
  font-family: "Rodnik";
  font-size: 16px;
  color: white !important;
}

.ant-collapse .ant-collapse-content {
  background: transparent;
}

/*  */

.ant-collapse-content-box {
  padding-left: 16px !important;
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}

.ant-collapse-expand-icon {
  font-size: 18px;
  color: white;
  height: 29px !important;
}


/* */
body {
  background: rgb(34, 58, 78);
  padding-top: env(safe-area-inset-top);
  min-height: 100vh;
  min-height: -webkit-fill-available;
  width: 100% !important;
  overflow: hidden !important;
  overscroll-behavior: none !important; /* Заменяем contain на none */
  position: fixed; /* Фиксируем тело документа */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

@supports(padding: max(0px)) {
  body {
    padding-top: max(env(safe-area-inset-top), 20px); /* Запас для старых устройств */
  }
}

/*  */

.calculationDetails:last-child {
  margin-bottom: 200px;
}

.scroll {
  height: 380px;
  overflow: scroll;
}

@media (max-height: 852px) {
  .scroll {
    height: 340px;
  }
}

@media (max-height: 780px) {
  .scroll {
    height: 300px;
  }
}


